@media screen and (max-width:1110px)  {
    .tableHolder{
        display: flex;
        width: 100vw;
        justify-content: space-evenly;
    }
    .slides{
        width: 97%;
        background-color: rgb(36, 36, 36);
        min-height: 90vh;
    }
    .Table{
        min-width: 100%;
        color: rgb(236, 236, 236);
        border-radius: 10px;
        border-collapse: collapse;
    }
    
    .icon{
        height: 1.7rem;
        width: 1.7rem;
        margin-left: 0.5rem;
        margin-right: 0.5rem;
    }
    tr{
        -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
    }
    .Table td{
        flex:1;
        height: 3rem;
        cursor: pointer;
        border: 0;
    }


    .details{
        position: fixed;
        top: 0;
        left: 0;
        height: 100vh;
        width: 100vw;
        background-color: black;
        z-index: 999;
        color: white;
        display: flex;
        flex-direction: column;
        align-items: center;
        transform: translateX(100%);
        transition: all 0.3s ease;
    }
    .details.active{
        transform: translateX(0%);
    }

    .backBtn{
        position: fixed;
        top: 1.5rem;
        right: 1.5rem;
        border: 2px solid white;
        border-radius: 10px;
        outline: none;
        background-color: transparent;
        color: white;
        height: 2.5rem;
        width: 5rem;
        font-size: 1rem;
    }
    .backBtn:active{
        background-color: rgb(58, 58, 58);
        transform: scale(0.9);
    }

    .Table2{
        min-width: 90%;
        color: rgb(236, 236, 236);
        border-radius: 10px;
        border-collapse: collapse;
    }
    .Table2 td{
        flex:1;
        height: 2.5rem;
        cursor: default;
        border: none;
        user-select: none;
        font-size: 1.3rem;
        text-align: left;
    }
    .loaderHolder{
        height: 100%;
        width: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
    }
    .circleLoader{
        height: 6rem;
        width: 6rem;
        border: 5px dotted white;
        border-radius: 50%;
        animation: rotater 5s linear infinite ;
    }
    @keyframes rotater {
        from{
            transform: rotate(0deg);
        }
        to{
            transform: rotate(360deg);
        }
    }
    
    .icondata{
        display: flex;
        justify-content: center;
        align-items: center;
    }
    
    .Table tr:active{
        background-color: rgb(87, 87, 87);
    }
    
    
    .details h1{
        margin-top: 17vh;
        width: 100vw;
        margin-left: 2rem;
    }
}
