@media screen and (min-width:1109px)  {
    .header{
        position: sticky;
        top: 0;
        left: 0;
        height: 4rem;
        display: flex;
        width: 100vw;
        justify-content: left;
        align-items: center;
        background-color: rgb(19, 19, 19);
    }
    
    input{
        margin-left: 2rem;
        height: 1.7rem;
        border-radius: 5px;
        outline: none;
        border: 2px solid grey;
        background-color: rgb(71, 71, 71);
        font-size: 1.1rem;
        color: rgb(255, 255, 255);
    }
}