@media screen and (min-width:1109px)  {
.tableHolder{
    display: flex;
    width: 100vw;
    justify-content: space-evenly;
}

.Table{
    min-width: 100%;
    color: rgb(236, 236, 236);
    border-radius: 10px;
    border-collapse: collapse;
}

.Table2{
    min-width: 60%;
    color: rgb(236, 236, 236);
    border-radius: 10px;
    border-collapse: collapse;
}

.icon{
    height: 1.5rem;
    width: 1.5rem;
}
.Table td{
    flex:1;
    height: 2.5rem;
    cursor: pointer;
    border: 0;
}
.Table2 td{
    flex:1;
    height: 2.5rem;
    cursor: default;
    border: 0;
    user-select: none;
    font-size: 1.3rem;
    text-align: left;
}

.loaderHolder{
    height: 100%;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}
.circleLoader{
    height: 6rem;
    width: 6rem;
    border: 5px dotted white;
    border-radius: 50%;
    animation: rotater 5s linear infinite ;
}
@keyframes rotater {
    from{
        transform: rotate(0deg);
    }
    to{
        transform: rotate(360deg);
    }
}

.icondata{
    display: flex;
    justify-content: center;
    align-items: center;
}

.Table tr:hover{
    background-color: rgb(87, 87, 87);
}

.slides{
    width: 48%;
    background-color: rgb(36, 36, 36);
}
.slidesFloat{
    position: sticky;
    top: 5rem;
    width: 48%;
    height: 85vh;
    background-color: rgb(36, 36, 36);
    color: rgb(255, 255, 255);
    display: flex;
    flex-direction: column;
    align-items: center;
    border-radius: 10px;
}
.slidesFloat h1{
    margin-top: 20vh;
}

}