@media screen and (max-width:1110px)  {
    .header{
        position: sticky;
        top: 0;
        left: 0;
        height: 4rem;
        display: flex;
        width: 100vw;
        justify-content: space-between;
        align-items: center;
        background-color: rgb(19, 19, 19);
    }

    .menuIcon{
        height: 2.5rem;
        width: 2.5rem;
        display: flex;
        justify-content: center;
        align-items: center;
        color: white;
        margin-right: 0.2rem;
        border-radius:50%;
    }
    .menuIcon:active{
        background-color: rgb(54, 54, 54);
    }
    
    input{
        margin-left: 0.9rem;
        height: 2rem;
        border-radius: 5px;
        outline: none;
        border: 2px solid grey;
        background-color: rgb(71, 71, 71);
        font-size: 1.1rem;
        color: rgb(255, 255, 255);
    }

    .menuContainer{
        position: fixed;
        top: 0;
        left: 0;
        width: 100vw;
        height: 100vh;
        background-color: rgba(0, 0, 0, 0.534);
        z-index: 800;
        display: none;
    }
    .menuContainer.active{
        display: flex;
    }

    .menuItems{
        position: fixed;
        display: flex;
        flex-direction: column;
        align-items: center;
        background-color: rgb(88, 88, 88);
        top:4rem;
        left:100vw;
        width: 10rem;
        border-bottom-left-radius: 10px;
        border-top-left-radius: 10px;
        overflow: hidden;
        transition: 0.2s all ease;
        visibility: hidden;
        z-index: 999;
    }
    .menuItems.active{
        visibility: visible;
        transform: translateX(-100%);
        transition: 0.2s ease;
    }
    .menuItems ul{
        list-style: none;
        padding-left: 0;
        width: 100%;
        height: 100%;
        display: flex;
        flex-direction: column;
        align-items: center;
    }
    .menuItems li{
        min-height: 3rem;
        width: 100%;
        flex: 1;
        background-color: inherit;
        color: white;
        border: none;
        border-radius: 5px;
        outline: none;
        font-size: 1.25rem;
        font-weight: bold;
        user-select: none;
        display: flex;
        justify-content: center;
        align-items: center;
    }
    .menuItems li:active{
        background-color: grey;
    }
}
